<script>
import { Bar } from 'vue-chartjs'
  export default {
    extends: Bar,
    // props: {
    //   chartData: {
    //     type: Array | Object,
    //     required: false
    //   },
    //    options: {
    //     type: Array | Object,
    //     required: false
    //   },
    // },
    data () {
      return {
        gradient:null,
        chartData: {
          labels: [
            'January',
            'February',
            'March',
            'April',
            'May',
            'June',
            'July',
            'August',
            'September',
            'October',
            'November',
            'December'
          ],
          datasets: [
            {
              label: 'Sales',
              backgroundColor: '#1976d2',
              data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11]
            }
          ]
        },
        chartOptions: {
          responsive: true,
          maintainAspectRatio: false
        }
      }
    },
    // vue-chart-js
    mounted () {
      this.renderChart(this.chartData, this.chartOptions)
    }
  }
</script>
